import React from 'react';
import classNames from 'classnames';
import DefenseStatus from '@/components/DefenseStatus';
require('./style.scss');

const List: any = React.memo(
  React.forwardRef((props: any, ref?: any) => {
    const { className, children } = props;
    return (
      <div className={classNames('video-monitor-list-wrap', className)}>
        <div className='video-monitor-list-head'>
          <div className='video-monitor-list-head-no'>序号</div>
          <div className='video-monitor-list-head-name'>设备名称</div>
          <div className='video-monitor-list-head-state'>状态</div>
          <div className='video-monitor-list-head-handle'>操作</div>
        </div>
        <div className='video-monitor-list-wrap' ref={ref}>
          {children}
        </div>
      </div>
    );
  })
);

const Item: any = React.memo((props: any) => {
  const { number, name, status, onSelect } = props;
  return (
    <div className='video-monitor-item'>
      <div className='video-monitor-list-no'>{number}</div>
      <div className='video-monitor-list-name'>{name}</div>
      <div className='video-monitor-list-state'>
        <DefenseStatus status={status} />
      </div>
      <div className='video-monitor-list-handle'>
        <button type='button' onClick={onSelect}>
          选择
        </button>
      </div>
    </div>
  );
});

List.item = Item;

export { List, Item };

export default List;
