import React from "react";
import classNames from "classnames";
import dayjs from "dayjs";
import { observer, inject } from "mobx-react";
import { Tabs, Toast, DatePicker, Icon } from "antd-mobile";
import SubPage from "@/components/SubPage";
import SelectDevice from "./subPage/selectArea";
import RealTimeHandle from "./components/RealTimeHandle";
import {
  Container as PlayerContainer,
  RealTime as RealTimePlayer,
  PlayBack as PlayBackPlayer,
  HySlider as PlayBackSlider
} from "./components/Player";
import { toJS } from "mobx";
require("./style.scss");

@inject("videoMonitorStore")
@observer
class VideoMonitor extends React.Component<IProps, GState> {
  realTimeRef_0: any;
  realTimeRef_1: any;
  realTimeRef_2: any;
  realTimeRef_3: any;
  playBackRef: any;
  constructor(props: any) {
    super(props);
    this.realTimeRef_0 = React.createRef();
    this.realTimeRef_1 = React.createRef();
    this.realTimeRef_2 = React.createRef();
    this.realTimeRef_3 = React.createRef();
    this.playBackRef = React.createRef();
  }

  onTabChange = (tab: any, index: number) => {
    const {
      videoMonitorStore: { aisleNumber, tabChange }
    } = this.props;
    if (index === 0) {
      for (let i = 0; i < aisleNumber; i++) {
        if (this[`realTimeRef_${i}`] && this[`realTimeRef_${i}`].current) {
          this[`realTimeRef_${i}`].current.initPlayer();
        }
      }
      if (this.playBackRef && this.playBackRef.current) {
        this.playBackRef.current.destroyPlayer();
      }
    } else if (index === 1 && this.playBackRef && this.playBackRef.current) {
      for (let i = 0; i < aisleNumber; i++) {
        if (this[`realTimeRef_${i}`] && this[`realTimeRef_${i}`].current) {
          this[`realTimeRef_${i}`].current.destroyPlayer();
        }
      }
      this.playBackRef && this.playBackRef.current.initPlayer();
    }
    tabChange(index);
  };

  componentWillUnmount() {
    const {
      videoMonitorStore: { reset }
    } = this.props;
    reset();
  }

  render() {
    const {
      videoMonitorStore: {
        tabs,
        tab,
        aisleNumber,
        realTimesVideos_max,
        realTimesVideos_four,
        playBackVideo,
        playBackTime,
        isSearchDevice,
        curSelctArea,

        tabChange,
        onSearchDeviceStateChange,
        onDeviceSelectChange,
        onAreaSelectChange,
        updatePlayBackTime,

        realTimeMax,
        realTimeFour,
        realTimeRemoveSelect,
        realTimeRemoveAll
      }
    } = this.props;

    return (
      <SubPage history={this.props.history} title="视频子系统" bgColor="#F0EDF1">
        <SelectDevice
          show={isSearchDevice}
          onSelectChange={(device: any) => {
            if (!device.channelId) {
              Toast.info("通道ID为空", 1);
              return;
            }

            onDeviceSelectChange(device);
            setTimeout(() => {
              if (curSelctArea) {
                if (
                  curSelctArea.type === "realTime" &&
                  curSelctArea.i >= 0 &&
                  this[`realTimeRef_${curSelctArea.i}`] &&
                  this[`realTimeRef_${curSelctArea.i}`].current
                ) {
                  // this[
                  //     `realTimeRef_${curSelctArea.i}`
                  // ].current.initPlayer();
                } else if (curSelctArea.type === "playBaCK" && this.playBackRef && this.playBackRef.current) {
                  // this.playBackRef.current.initPlayer();
                }
              }
            }, 100);
          }}
          onClose={() => {
            onSearchDeviceStateChange(false);
          }}
        />
        <div className="video-monitor-content">
          <button
            className="video-monitor-select-btn"
            type="button"
            onClick={() => {
              if (tab === 0) {
                let i = -1;
                if (aisleNumber === 1) {
                  i = realTimesVideos_max.findIndex((item: any) => item.selected);
                } else if (aisleNumber === 4) {
                  i = realTimesVideos_four.findIndex((item: any) => item.selected);
                }
                if (i === -1) {
                  Toast.info("请先选择视频设备", 1);
                  return;
                }
                onSearchDeviceStateChange(true, "realTime", i || 0);
              } else if (tab === 1) {
                onSearchDeviceStateChange(true, "playBack");
              }
            }}
          >
            选择视频设备
          </button>
          <Tabs tabs={tabs} onChange={this.onTabChange} page={tab} animated={false} useOnPan={false} swipeable={false}>
            <div className="video-realTime">
              <div>
                <div
                  className={classNames("video-realTime-container", {
                    "video-realTime-container_max": aisleNumber === 1,
                    "video-realTime-container_four": aisleNumber === 4
                  })}
                >
                  {Array.from({ length: aisleNumber }, (v, k) => k).map((v: any, i: number) => {
                    let realTimesVideo: any;

                    if (aisleNumber === 1) {
                      realTimesVideo = realTimesVideos_max[i];
                    } else if (aisleNumber === 4) {
                      realTimesVideo = realTimesVideos_four[i];
                    }

                    return (
                      <PlayerContainer
                        key={`${aisleNumber}-${realTimesVideo.realId}-${i}`}
                        className="video-realTime-content"
                        name={realTimesVideo.name}
                        isEmpty={realTimesVideo.isEmpty}
                        selected={realTimesVideo.selected}
                        onSelectChange={() => {
                          onAreaSelectChange(i);
                        }}
                      >
                        {(width: number) => (
                          <RealTimePlayer
                            {...realTimesVideo}
                            index={i}
                            width={width}
                            ref={this[`realTimeRef_${i}`]}
                            streamId={1}
                            onAdd={() => {
                              onSearchDeviceStateChange(true, "realTime", i);
                            }}
                          ></RealTimePlayer>
                        )}
                      </PlayerContainer>
                    );
                  })}
                </div>
                <RealTimeHandle max={realTimeMax} four={realTimeFour} removeSelect={realTimeRemoveSelect} removeAll={realTimeRemoveAll} />
              </div>
            </div>
            <div className="video-playBack">
              <div>
                <PlayerContainer
                  className="video-playBack-content"
                  name={playBackVideo.name}
                  isEmpty={playBackVideo.isEmpty}
                  selected={playBackVideo.selected}
                >
                  {(width: number) => (
                    <PlayBackPlayer
                      ref={this.playBackRef}
                      {...playBackVideo}
                      date={playBackTime}
                      index={0}
                      width={width}
                      streamId={0}
                      onAdd={() => {
                        onSearchDeviceStateChange(true, "playBack");
                      }}
                    ></PlayBackPlayer>
                  )}
                </PlayerContainer>
                {false && playBackVideo && playBackTime && <PlayBackSlider player={this.playBackRef} />}
                <div className="video-playBack-datePicker">
                  <div className="video-playBack-datePicker-label">回放日期</div>
                  <div
                    className="video-playBack-datePicker-value"
                    onClick={() => {
                      if (!playBackVideo || !playBackVideo.id) {
                        Toast.info("请先选择视频设备", 1);
                      }
                    }}
                  >
                    <DatePicker
                      mode="date"
                      title="请选择回放日期"
                      value={playBackTime}
                      maxDate={new Date()}
                      disabled={!playBackVideo || !playBackVideo.id}
                      onChange={date => {
                        console.log("date", date);
                        updatePlayBackTime(date);
                        setTimeout(() => {
                          if (this.playBackRef && this.playBackRef.current) {
                            this.playBackRef.current.initPlayer();
                          }
                        }, 100);
                      }}
                    >
                      <div>
                        <span>{playBackTime ? dayjs(playBackTime).format("YYYY-MM-DD") : "请选择回放日期"}</span>
                        <Icon type="down" size="sm" />
                      </div>
                    </DatePicker>
                  </div>
                </div>
              </div>
            </div>
          </Tabs>
        </div>
      </SubPage>
    );
  }
}

export default VideoMonitor;
