import React, { useEffect, useState, useMemo } from "react";
import classNames from "classnames";
import dayjs from "dayjs";
import { Slider, WingBlank } from "antd-mobile";
import DataApiService from "@/service/http";

require("./style.scss");

class Container extends React.Component<GProps, GState> {
  containerRef: any;
  constructor(props: IProps) {
    super(props);
    this.state = {
      size: { width: 0 }
    };
    this.containerRef = React.createRef();
  }

  componentDidMount() {
    this.setState({
      size: { width: this.containerRef.current.clientWidth }
    });
  }

  render() {
    const { className, name, isEmpty, children, selected, onSelectChange } = this.props;
    const { size } = this.state;
    return (
      <div className={classNames(className)}>
        <div className={classNames({ "video-container-wrap_selected": selected })}>
          <div
            className={classNames("video-container", {
              "video-container_selected": selected
            })}
            ref={this.containerRef}
          >
            {size.width && children(size.width)}
          </div>
          <div
            className="video-container-title"
            onClick={() => {
              if (!isEmpty && onSelectChange) {
                onSelectChange(!selected);
              }
            }}
          >
            {name}
          </div>
        </div>
      </div>
    );
  }
}
class RealTime extends React.Component<IProps, GState> {
  player: any;

  componentDidMount() {
    console.log("预览播放器重新创建", this.props);
    const { id, realId, channelId } = this.props;
    if (id && realId && channelId) {
      this.initPlayer();
    }
  }

  initPlayer() {
    console.log("预览播放器初始化", this.props);
    const { id, realId, channelId, streamId, width, index } = this.props;
    let url = "";
    if (!id || !realId) return;

    this.destroyPlayer();
    DataApiService.getReplayServerInfo({
      channelId: realId,
      streamType: streamId,
      protocolType: 2,
      addrType: 2
    }).then(res => {
      const urls = res.result.intraUrl.split("play_real");
      url = `ws://${location.host}/previewPlayer/play_real${urls[1]}`;
      console.log("预览路径", url);
      const HYPlayerParams: any = {
        url,
        container: `#realTimePlayer-${realId}-${index}`,
        live: true,
        loop: false,
        autoPlay: false,
        hasAudio: true,
        control: true,
        muted: false,
        cors: true,
        volume: 0,
        width: width,
        height: Math.ceil((width * PLAYER_HEIGHT) / PLAYER_WIDTH),
        duration: 0,
        decoderURL: "./lib/core/decoder.js"
      };
      this.player = new HYPlayer(HYPlayerParams);
    });

    //url = `ws://${location.host}/previewPlayer/play_real/${id}/${Number(channelId) - 1}/${streamId}`;
    // url = `ws://192.168.8.236:32200/47ca3004-5180-489e-b251-d326b49603f5/36/1`;
  }

  componentWillUnmount() {
    console.log("预览播放器卸载", this.props);
    this.destroyPlayer();
  }

  destroyPlayer = () => {
    if (this.player) {
      this.player.destroy();
      this.player = null;
    }
  };

  render() {
    const { id, realId, index, width, onAdd } = this.props;
    if (!id || !realId) {
      return <Add width={width} onAdd={onAdd} />;
    }
    return (
      <div
        id={`realTimePlayer-${realId}-${index}`}
        style={{
          width: width,
          height: Math.ceil((width * PLAYER_HEIGHT) / PLAYER_WIDTH)
        }}
      ></div>
    );
  }
}

const PlayBack = React.forwardRef((props: IProps, ref: any) => {
  let player: any;
  const { id, realId, channelId, date, width, index, onAdd } = props;
  const [load, setload] = useState(false);

  const formatTime = (time: any) => {
    return time.format("YYYY-MM-DDTHH:mm:ss");
  };

  const destroyPlayer = () => {
    if (player) {
      player.destroy();
      player = null;
    }
  };

  const initPlayer = (time: number) => {
    let url = "";
    if (!id || !realId || !date) return;
    destroyPlayer();

    DataApiService.getReplayServerInfo({
      channelId: realId,
      protocolType: 2,
      addrType: 1,
      recordType: 2,
      startTime: formatTime(dayjs(date).startOf("day")),
      stopTime: formatTime(dayjs(date).endOf("day"))
    }).then(res => {
      console.log("请求回放后端接口", res.result.intraUrl);
      const urls = res.result.intraUrl.split("play_back");
      url = `ws://${location.host}/replayPlayer/play_back${urls[1]}`;
      //url = res.result.intraUrl;

      console.log("回放路径", url);
      setload(true);
      const HYPlayerParams: any = {
        url,
        poster: "", //封面
        debug: true,
        live: false,
        loop: false,
        autoPlay: false,
        hasAudio: true,
        control: true,
        muted: false,
        cors: true,
        volume: 0.7,
        duration: time / 1000,
        container: `#playBackPlayer-${realId}-${index}`,
        width: width,
        height: Math.ceil((width * PLAYER_HEIGHT) / PLAYER_WIDTH),
        decoderURL: "./lib/core/decoder.js"
      };
      player = new HYPlayer(HYPlayerParams);
      player.on("ready", () => {
        setload(false);
      });
    });
  };

  const getTimeCount = (videoTypes: any): number => {
    if (Array.isArray(videoTypes)) {
      const vMap = new Map();
      // 过滤重复时间
      videoTypes.forEach(item => {
        let id = `${dayjs(item.startTime).valueOf()} -${dayjs(item.endTime).valueOf()}`;
        if (!vMap.has(id)) {
          vMap.set(id, item);
        }
      });
      // 根据开始时间排序
      let videos = [...vMap.values()]
        .map(item => ({
          ...item,
          sort: dayjs(item.startTime).valueOf()
        }))
        .sort((a: any, b: any) => a.sort - b.sort);
      // 计算总时长毫秒（考虑交叉的情况）
      let count = videos.reduce((count, video, index, videos) => {
        let len: number = 0;
        if (dayjs(video.startTime).isBefore(dayjs(video.endTime))) {
          if (videos[index - 1] && dayjs(videos[index - 1].endTime).valueOf() > dayjs(video.startTime).valueOf()) {
            len = dayjs(video.endTime).valueOf() - dayjs(videos[index - 1].endTime).valueOf();
          } else {
            len = dayjs(video.endTime).valueOf() - dayjs(video.startTime).valueOf();
          }
        }
        return count + len;
      }, 0);

      console.log("总时长", count);
      return count;
    }

    return 0;
  };

  useEffect(() => {
    if (id && realId && channelId && date) {
      setload(true);
      DataApiService.replayVideoQuery({
        devIds: [realId],
        startTime: dayjs(date).startOf("day").format("YYYY-MM-DD HH:mm:ss"),
        endTime: dayjs(date).endOf("day").format("YYYY-MM-DD HH:mm:ss")
      }).then(res => {
        const { result } = res;
        if (result && result[0] && Array.isArray(result[0].videoTypes)) {
          let time: number = getTimeCount(result[0].videoTypes);
          initPlayer(time);
        }
      });
      // initPlayer();
    }
    return () => {
      destroyPlayer();
    };
  }, [id, realId, channelId, date]);

  if (!id || !realId) {
    return <Add width={width} onAdd={onAdd} />;
  }
  if (!date) {
    return (
      <div
        className="playBackPlayer-tips"
        style={{
          width: width,
          height: Math.ceil((width * PLAYER_HEIGHT) / PLAYER_WIDTH)
        }}
      >
        <span>请选择日期</span>
      </div>
    );
  }
  return (
    <div
      style={{
        position: "relative",
        width: width,
        height: Math.ceil((width * PLAYER_HEIGHT) / PLAYER_WIDTH)
      }}
    >
      <div
        className="playBackPlayer-loading"
        style={{
          display: load ? "flex" : "none"
        }}
      >
        <span>加载中...</span>
      </div>
      <div id={`playBackPlayer-${realId}-${index}`} style={{ width: width, height: Math.ceil((width * PLAYER_HEIGHT) / PLAYER_WIDTH) }}></div>
    </div>
  );
});

const PLAYER_WIDTH = 800;
const PLAYER_HEIGHT = 450;

class Add extends React.Component<GProps, GState> {
  render() {
    const { width, onAdd } = this.props;
    return (
      <div
        className="video-container-empty"
        style={{
          height: Math.ceil((width * PLAYER_HEIGHT) / PLAYER_WIDTH)
        }}
      >
        <span
          className="video-container-icon video-container-icon_add"
          onClick={e => {
            e.stopPropagation();
            onAdd && onAdd();
          }}
        ></span>
      </div>
    );
  }
}

const HySlider = (props: any) => {
  const [silder, setSlider] = useState(0);
  const [player, setPlayer] = useState(undefined);

  useEffect(() => {
    if (props.player.current) {
      const { player } = props.player.current;
      setPlayer(player);
    }
  }, [props.player]);

  const onAfterChange = (val: number) => {
    const time: any = (val / 100) * player.duration;
    player.seeked(parseInt(time));
  };
  const onChange = (val: number) => {
    setSlider(val);
  };

  return useMemo(
    () => (
      <>
        <WingBlank size="lg" />
        <Slider value={silder} min={0} max={100} onAfterChange={onAfterChange} onChange={onChange} />
        <WingBlank size="lg" />
      </>
    ),
    [silder]
  );
};

export { Container, RealTime, PlayBack, HySlider };
