import React from "react";
import classNames from "classnames";
import { observer, inject } from "mobx-react";
import { SearchBar, Tabs, Toast } from "antd-mobile";
import AreaSelect from "@/components/AreaSelect2";
import AreaList from "@/components/AreaList";
import CommonListView from "@/components/CommonListView";
import { List, Item } from "../../components/list";
require("./style.scss");

@inject("selectAreaStore")
@observer
class SelectArea extends React.Component<GProps, GState> {
  ListRef: any;
  equipmentsRef: any;
  constructor(props: IProps) {
    super(props);
    this.ListRef = React.createRef();
    this.equipmentsRef = React.createRef();
  }

  componentDidMount() {
    const {
      selectAreaStore: { updateHeight }
    } = this.props;
    updateHeight(document.documentElement.clientHeight - this.ListRef.current.offsetTop - document.querySelector(".am-navbar").offsetHeight - 5);
  }

  componentWillUnmount() {
    const {
      selectAreaStore: { reset }
    } = this.props;
    reset();
    // this.equipmentsRef.current.scrollTo(0, 0);
  }

  renderRow = (video: any) => {
    const { onSelectChange } = this.props;
    return (
      <Item
        key={video.realId}
        number={video.number}
        name={`${video.name || "无"}/${video.channelName || "无"}`}
        status={video.status}
        onSelect={() => {
          onSelectChange && onSelectChange(video);
        }}
      />
    );
  };

  render() {
    const {
      show,
      onClose,
      selectAreaStore: { height, curArea, searchValue, onSearchValueChange, searchText, updateCurArea, updateSearchText, fetchVideos }
    } = this.props;
    return (
      <div
        className={classNames("video-monitor-search", {
          "video-monitor-search_open": show
        })}
      >
        <SearchBar
          className="video-monitor-search-bar"
          placeholder="在结果中搜索"
          value={searchValue}
          onChange={onSearchValueChange}
          showCancelButton={false}
          onSubmit={value => {
            updateSearchText(value);
            this.equipmentsRef.current.reset();
          }}
          onClear={() => {
            // 清除搜索
            updateSearchText("");
            this.equipmentsRef.current.reset();
          }}
          onCancel={() => {
            // 关闭搜索
            onClose();
          }}
        />
        <div className="video-monitor-search-content">
          <AreaSelect
            onChange={(idKey: string, id: string) => {
              // 接受区域树改变
              updateCurArea({ idKey, id });
              this.equipmentsRef.current.reset();
            }}
            hasVideo
          />
          <List className="video-monitor-search-list" ref={this.ListRef}>
            {height && (
              <CommonListView
                ref={this.equipmentsRef}
                height={height}
                fetchList={fetchVideos}
                limit={20}
                renderRow={this.renderRow}
                params={{ ...curArea, searchText }}
              />
            )}
          </List>
        </div>
      </div>
    );
  }
}

export default SelectArea;
