import React from "react";

class RealTimeHandle extends React.PureComponent<GProps, GState> {
  render() {
    const { max, four, removeSelect, removeAll } = this.props;
    return (
      <div className="video-realTime-handle">
        <div className="video-realTime-handle-item" onClick={max}>
          <div className="video-realTime-handle-icon video-realTime-handle-icon_one"></div>
          <span className="video-realTime-handle-text">最大化</span>
        </div>
        <div className="video-realTime-handle-item" onClick={four}>
          <div className="video-realTime-handle-icon video-realTime-handle-icon_four"></div>
          <span className="video-realTime-handle-text">四通道</span>
        </div>
        <div className="video-realTime-handle-item" onClick={removeSelect}>
          <div className="video-realTime-handle-icon video-realTime-handle-icon_remove">
            <span></span>
          </div>
          <span className="video-realTime-handle-text">移除选中</span>
        </div>
        <div className="video-realTime-handle-item" onClick={removeAll}>
          <div className="video-realTime-handle-icon video-realTime-handle-icon_removeAll"></div>
          <span className="video-realTime-handle-text">全部移除</span>
        </div>
      </div>
    );
  }
}

export default RealTimeHandle;
